import { FC } from 'react';

// Components
import Typography from '../Typography';

// Types
import { TestMetadata } from '../types';
export interface FormReadOnlyFieldTextValueProps {
  testMetadata?: TestMetadata;
  truncate?: 'truncate' | 'wrap';
  value?: string;
  fallbackValue?: string;
}

const FormReadOnlyFieldTextValue: FC<FormReadOnlyFieldTextValueProps> = ({
  value,
  truncate,
  testMetadata,
  fallbackValue = '-',
}) => (
  <Typography
    truncate={truncate === 'truncate'}
    wrap={truncate === 'wrap'}
    variant="T15"
    {...testMetadata}
  >
    {value || fallbackValue}
  </Typography>
);

export default FormReadOnlyFieldTextValue;
