import { FC, ReactElement, ReactNode } from 'react';
import classNames from 'classnames';
import { TippyProps } from '@tippyjs/react';

// Components
import Popover from '../Popover/';

// Styles
import './Tooltip.scss';
import 'tippy.js/dist/tippy.css';
import 'tippy.js/themes/light.css';
import 'tippy.js/themes/light-border.css';
import 'tippy.js/animations/scale.css';

// Types
import { TestMetadata } from 'types';

export interface TooltipProps {
  children: ReactNode;
  tippyProps?: TippyProps;
  className?: string;
  contentClassName?: string;
  tooltipContent: ReactElement | string;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const defaultTippyProps: TippyProps = {
  maxWidth: 'none',
  interactive: true,
  arrow: true,
};

const Tooltip: FC<TooltipProps> = ({
  children,
  className,
  contentClassName,
  tooltipContent,
  tippyProps,
  testMetadata,
}) => {
  return (
    <Popover
      className={classNames('sb-tooltip', className)}
      popoverContent={tooltipContent}
      tippyProps={{ ...defaultTippyProps, ...tippyProps }}
      {...testMetadata}
      {...{ contentClassName }}
    >
      {children}
    </Popover>
  );
};

export default Tooltip;
