import format from 'date-fns/format';

// Components
import FormReadOnlyFieldTextValue from '../FormReadOnlyFieldTextValue';

// Types
import { FC } from 'react';
import { TestMetadata } from '../types';

export interface FormReadOnlyFieldDateValueProps {
  testMetadata?: TestMetadata;
  value?: Date | string;
  dateFormat?: string;
  fallbackValue?: string;
}

const FormReadOnlyFieldDateValue: FC<FormReadOnlyFieldDateValueProps> = ({
  value,
  testMetadata,
  dateFormat = 'MM/dd/yyy',
  fallbackValue,
}) => {
  const parsedDate = value
    ? typeof value === 'string'
      ? new Date(Date.parse(value))
      : value
    : null;

  return (
    <FormReadOnlyFieldTextValue
      testMetadata={testMetadata}
      value={parsedDate ? format(parsedDate, dateFormat) : ''}
      fallbackValue={fallbackValue}
    />
  );
};

export default FormReadOnlyFieldDateValue;
