// Type
import { FC } from 'react';
import { TestMetadata } from '../types';

// Components
import FormReadOnlyFieldTextValue from '../FormReadOnlyFieldTextValue';
import Container from '../Container';
import FormReadOnlyFieldDateValue from '../FormReadOnlyFieldDateValue';

// Styles
import './FormReadOnlyFieldDateRangeValue.scss';

export interface FormReadOnlyFieldDateRangeValueProps {
  testMetadata?: {
    startDate?: TestMetadata;
    endDate?: TestMetadata;
    emptyValue?: TestMetadata;
  };
  startDate?: Date | string;
  endDate?: Date | string;
  dateFormat?: string;
}

const FormReadOnlyFieldDateRangeValue: FC<
  FormReadOnlyFieldDateRangeValueProps
> = ({ startDate, endDate, testMetadata, dateFormat }) => {
  if (!startDate && !endDate) {
    return (
      <FormReadOnlyFieldTextValue testMetadata={testMetadata?.emptyValue} />
    );
  }

  return (
    <Container className="sb-form-readonly-field-date-range-value">
      <FormReadOnlyFieldDateValue
        dateFormat={dateFormat}
        testMetadata={testMetadata?.startDate}
        value={startDate}
        fallbackValue=""
      />
      â€“
      <FormReadOnlyFieldDateValue
        dateFormat={dateFormat}
        testMetadata={testMetadata?.endDate}
        value={endDate}
        fallbackValue=""
      />
    </Container>
  );
};

export default FormReadOnlyFieldDateRangeValue;
